module.exports = ".clients {\n  max-width: 1000px;\n  margin: 0 auto;\n}\n\n.fa-plus-circle {\n  font-size: 1.3em;\n}\n\n.card {\n  margin-top: 20px;\n}\n\nli {\n  display: grid;\n  grid-template-columns: 1fr auto;\n  grid-template-areas:\n    'name controls'\n    'email controls';\n  align-items: center;\n  border-bottom: 1px dotted black;\n  padding: 10px 20px;\n}\n\nli:last-of-type {\n  border: none;\n}\n\nli span:nth-child(1) {\n  grid-area: name;\n}\n\nli span:nth-child(2) {\n  grid-area: email;\n}\n\n.controls {\n  grid-area: controls;\n  position: relative;\n}\n\n.controls:focus-within > .dropdown {\n  display: flex;\n}\n\n.dropdown {\n  display: none;\n  z-index: 3;\n  min-width: 120px;\n  position: absolute;\n  top: 100%;\n  right: 0;\n  flex-direction: column;\n}\n\n.dropdown > button {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\n.filter {\n  display: flex;\n  flex-direction: column;\n  align-items: start;\n}\n\ninput {\n  border: 1px solid var(--dark);\n  padding: 10px 15px;\n  min-width: 250px;\n  width: 30%;\n}\n\ninput::-webkit-input-placeholder {\n  font-size: 16px;\n}\n\ninput:-ms-input-placeholder {\n  font-size: 16px;\n}\n\ninput::-ms-input-placeholder {\n  font-size: 16px;\n}\n\ninput::placeholder {\n  font-size: 16px;\n}\n\n@media (min-width: 600px) {\n  li {\n    grid-template-columns: 1fr auto 1fr;\n    grid-template-areas: 'name email controls';\n  }\n  .controls {\n    justify-self: end;\n  }\n  .filter {\n    flex-direction: row;\n    justify-content: space-between;\n    align-items: center;\n  }\n}\n\n@media (min-width: 900px) {\n  .controls > button {\n    display: none;\n  }\n  .dropdown {\n    position: initial;\n    display: flex;\n    justify-content: space-around;\n    flex-direction: row;\n  }\n  .dropdown > button {\n    margin-left: 5px;\n  }\n  .dropdown > button i {\n    margin-left: 5px;\n  }\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"